/* A pop up template that display the form to fill out and a submit button to send the info to the BE */
import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import axios from 'axios';
// import jwt_decode from "jwt-decode";

const adminName = localStorage.getItem("adminName")
// const userID= localStorage.getItem("id")
// const userEmail = localStorage.getItem("email")
// const user_type = localStorage.getItem('user_type')

export default function AdminPopUpAddUsers() {
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
      <Button variant="contained" color="primary" onClick={handleClickOpen}>
        Add
      </Button>
      <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">Update your information</DialogTitle>
        <DialogContent>
          <DialogContentText>
            To add a user, please enter the information below
          </DialogContentText>
          <TextField
            autoFocus
            margin="dense"
            id="u-username"
            label="Username"
            type="text"
            fullWidth
          />
          <TextField
            autoFocus
            margin="dense"
            id="u-email"
            label="Email Address"
            type="email"
            fullWidth
          />

          <TextField
            autoFocus
            margin="dense"
            id="accessLevel"
            label="Access Level"
            type="text"
            fullWidth
          />
          <TextField
            autoFocus
            margin="dense"
            id="user-set-password"
            label="User Password"
            type="password"
            fullWidth
          />
          <TextField
            autoFocus
            margin="dense"
            id="u-o-password"
            label="Admin password"
            type="password"
            fullWidth
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleAdd} color="primary">
            Add
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}

/* Function to send the updated info to the back end for modification */
function handleAdd(e){
    e.preventDefault();

    //Form validation
    var addedUsername = document.getElementById('u-username').value
    var addedEmail = document.getElementById('u-email').value
    var AdminPassword = document.getElementById('u-o-password').value
    var level = parseInt(document.getElementById('accessLevel').value)
    var userPassword = document.getElementById('user-set-password').value

    if(AdminPassword == ""){
        alert("Admin password needed to add new users")
    }

    else if(addedUsername == "" && addedEmail == "" && level == "" && userPassword == ""){
      alert("No new information are added!")
    }

    else if(addedUsername == "" || addedEmail == "" || level == "" || userPassword == ""){
      alert("Missing required fields!")
    }
    else{
        /*Before updating the user account, we will need to check if the user making the changes is the same person by 
        performing a light validation using the stored cookie session */
        let validation = {
          username: adminName,
          password: AdminPassword
      }

      //let request = validateForms(addedUsername, addedEmail, AdminPassword, level, userType)
      //Determine to add user or professors depends on the user_type
      let request = {
        username: addedUsername,
        email: addedEmail,
        level: level,
        password: userPassword
      }
      /* Back end connection using axios */
      axios.post(`http://206.189.194.211:8000/loginAdmin`, validation)
      // axios.post('http://localhost:8000/loginAdmin', validation)
      .then(response => {
          var errorMessage = response.data.error
          console.log(request)
          //Unable to authenticate admin password.
          if(errorMessage === "Invalid password" || errorMessage === "Invalid Username"){
              alert(errorMessage)
          }
          else{
            // axios.post(`http://206.189.194.211:8000/updateUser/${userID}`, request)
            axios.post('http://206.189.194.211:8000/register', request)
            // axios.post('http://localhost:8000/register', request)
            .then(res => {
                //error handling
                console.log(res)
                var success = res.data.result
                var failure = res.data.error
                //alert information to the user
                if(failure != ""){
                  alert(failure)
                }

                else{
                  alert(success)
                  window.location.reload(false); 
                }
            })
            .catch(err => {
                console.log(err);
                console.log(request)
                alert(err)
            })
          }
      })
      .catch(err => {
          console.log(err);
          
      })

    }
}


