import React from "react";
import { Grid } from "@material-ui/core";
import UserStatsCardView from './UserStatsCardView';
import axios from "axios";
class DisplayQuestionAnswersForAdmin extends React.Component{
    //create question+answer state to move around functions
    state = {
        questions: [],
        answers: []
    }

    //mounting the api call to request question and answer data from the backend
    async componentDidMount(){
        //making two api request for answer and question data
        const [questionRes, answerResp] = await Promise.all([
            axios.get('http://206.189.194.211:8080/getAllQuestions'),
            axios.get('http://206.189.194.211:8080/getAllAnswers')
        ]);
        //setting the state of the two api request into the state
        this.setState({
            questions: questionRes.data,
            answers: answerResp.data
        })

    }
    render(){
        const {
            questions, answers
        } = this.state

        const questionAnswerCountData = [
            {
                userType: "QUESTIONS",
                userCount: questions.length
            },
            {
                userType: "ANSWERS",
                userCount: answers.length
            }
            
        ]
        // console.log(questions)
        // console.log(answers)
        //Function to get all user obect from the database
        const getUserTypeObject = data => {
            return(
                <Grid item xs={12} sm={4}>
                    <UserStatsCardView {...data} />
                </Grid>
            )
        }
        return(
            <div className="display-info-main-container">
                <Grid container spacing={4} style={{paddingLeft: "290px", paddingRight: "10px"}}>
                    {questionAnswerCountData.map(data => getUserTypeObject(data))}
                </Grid>
                <br></br>

            </div>
            
        )
    }
}


export default DisplayQuestionAnswersForAdmin;