//Creating a protected routes to only allow admin with accurate credentials can log in
import React from 'react';
import { Redirect } from 'react-router-dom';

class ProtectedRoutes_ADMIN extends React.Component{
    render(){
        /*Getting the contents of the props being sent into the class. The props will contain 
        path that needed to be protected.*/
        const Component = this.props.component;
        const isAdmin = localStorage.getItem('isAdmin');
        //checking if the user is an admin or not
        return isAdmin == "admin" ? (
            //if the user is really is an admin, send them to the admin site
            <Component />

        ) : (
            //otherwise, redirect them to the admin login page. 
            <Redirect to={{pathname: '/AdminLogin'}} />
        )
    }
}


export default ProtectedRoutes_ADMIN;