import React, { Component } from 'react';
// import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios';
// const professor_name = localStorage.getItem('professor_name')
const professor_id = localStorage.getItem('professor_id')
const user_type = localStorage.getItem('user_type')
class AddSubscriptionForm extends Component{
    /* We have defined the three initial states. Each state is for one checkbox. */
    state = {
        COMPUTER_SCIENCE_BASIC_CONCEPT: false,
        WEB_PROGRAMMING: false,
        DATA_STRUCTURE_AND_ALGORITHM: false,
        COMPONENT_BASED_PROGRAMMING: false,
        COMPUTER_ARCHITECTURE: false,
        OPERATING_SYSTEM: false,
        COMPUTER_NETWORK: false,
        MOBILE_PROGRAMMING: false,
        PROGRAMMING_LANGUAGES: false,
        DATABASE_THEORY: false,
        SOFTWARE_ENGINEERING: false,
        COMPUTER_SECURITY_PRIVACY: false
    }

    toggleChange_COMPUTER_SCIENCE_BASIC_CONCEPT = () => {
        this.setState(prevState => ({
            COMPUTER_SCIENCE_BASIC_CONCEPT: !prevState.COMPUTER_SCIENCE_BASIC_CONCEPT,
        }));
      }

    toggleChange_WEB_PROGRAMMING = () => {
    this.setState(prevState => ({
        WEB_PROGRAMMING: !prevState.WEB_PROGRAMMING,
    }));
    }

    toggleChange_DATA_STRUCTURE_AND_ALGORITHM = () => {
    this.setState(prevState => ({
        DATA_STRUCTURE_AND_ALGORITHM: !prevState.DATA_STRUCTURE_AND_ALGORITHM,
    }));
    }
    toggleChange_COMPONENT_BASED_PROGRAMMING = () => {
        this.setState(prevState => ({
            COMPONENT_BASED_PROGRAMMING: !prevState.COMPONENT_BASED_PROGRAMMING,
        }));
    }
    toggleChange_COMPUTER_ARCHITECTURE = () => {
        this.setState(prevState => ({
            COMPUTER_ARCHITECTURE: !prevState.COMPUTER_ARCHITECTURE,
        }));
    }

    toggleChange_OPERATING_SYSTEM = () => {
        this.setState(prevState => ({
            OPERATING_SYSTEM: !prevState.OPERATING_SYSTEM,
        }));
    }
    toggleChange_COMPUTER_NETWORK = () => {
        this.setState(prevState => ({
            COMPUTER_NETWORK: !prevState.COMPUTER_NETWORK,
        }));
    }
    toggleChange_MOBILE_PROGRAMMING = () => {
        this.setState(prevState => ({
            MOBILE_PROGRAMMING: !prevState.MOBILE_PROGRAMMING,
        }));
    }
    toggleChange_PROGRAMMING_LANGUAGES = () => {
        this.setState(prevState => ({
            PROGRAMMING_LANGUAGES: !prevState.PROGRAMMING_LANGUAGES,
        }));
    }
    toggleChange_DATABASE_THEORY = () => {
        this.setState(prevState => ({
            DATABASE_THEORY: !prevState.DATABASE_THEORY,
        }));
        }
    toggleChange_SOFTWARE_ENGINEERING = () => {
        this.setState(prevState => ({
            SOFTWARE_ENGINEERING: !prevState.SOFTWARE_ENGINEERING,
        }));
        }

    toggleChange_COMPUTER_SECURITY_PRIVACY = () => {
        this.setState(prevState => ({
            COMPUTER_SECURITY_PRIVACY: !prevState.COMPUTER_SECURITY_PRIVACY,
        }));
        }
    /* Converting checked value into string */ 
    onSubmit = (e) => {
        e.preventDefault();
        let arr = [];
        //looping through the state/checkboxes and if any of them is checked add it into the array
        for(var key in this.state){
            console.log(key)
            if(this.state[key] === true){
                arr.push(key.toString());
            }
        }

        let request = {
            subscription: arr
        };

        console.log(request)        
        axios.post(`http://206.189.194.211:8081/addSubscription/${professor_id}/${user_type}`, request)
        // axios.post(`http://localhost:8081/addSubscription/${professor_id}/${user_type}`, request)
        .then(res => {

            console.log(res.data)
            var goodResp = res.data.result
            var badResp = res.data.error
            //if no error response back from the backend
            if(!badResp){
                alert(goodResp)
                window.location = "/professor"

            }

            else(
                alert(badResp)
            )
        });
    }
    
    render(){
        return(
            <div className="selection-container">
                <h2>Please select your topic selection here!</h2>
                <hr />
                <form onSubmit = {this.onSubmit}>
                    <div className="form-check">
                        <label className="form-check-label">
                        <input type="checkbox"
                            checked={this.state.COMPUTER_SCIENCE_BASIC_CONCEPT}
                            onChange={this.toggleChange_COMPUTER_SCIENCE_BASIC_CONCEPT}
                            className="form-check-input"
                        />
                        COMPUTING SCIENCE BASIC CONCEPTS 
                        </label>
                    </div>
                    <div className="form-check">
                        <label className="form-check-label">
                        <input type="checkbox"
                            checked={this.state.WEB_PROGRAMMING}
                            onChange={this.toggleChange_WEB_PROGRAMMING}
                            className="form-check-input"
                        />
                        WEB PROGRAMMING
                        </label>
                    </div>
                    <div className="form-check">
                        <label className="form-check-label">
                        <input type="checkbox"
                            checked={this.state.DATA_STRUCTURE_AND_ALGORITHM}
                            onChange={this.toggleChange_DATA_STRUCTURE_AND_ALGORITHM}
                            className="form-check-input"
                        />
                        DATA STRUCTURE AND ALGORITHM
                        </label>
                    </div>


                    <div className="form-check">
                        <label className="form-check-label">
                        <input type="checkbox"
                            checked={this.state.COMPONENT_BASED_PROGRAMMING}
                            onChange={this.toggleChange_COMPONENT_BASED_PROGRAMMING}
                            className="form-check-input"
                        />
                        COMPONENT BASED PROGRAMMING
                        </label>
                    </div>
                    <div className="form-check">
                        <label className="form-check-label">
                        <input type="checkbox"
                            checked={this.state.COMPUTER_ARCHITECTURE}
                            onChange={this.toggleChange_COMPUTER_ARCHITECTURE}
                            className="form-check-input"
                        />
                        COMPUTER ARCHITECTURE
                        </label>
                    </div>
                    <div className="form-check">
                        <label className="form-check-label">
                        <input type="checkbox"
                            checked={this.state.OPERATING_SYSTEM}
                            onChange={this.toggleChange_OPERATING_SYSTEM}
                            className="form-check-input"
                        />
                        OPERATING SYSTEM
                        </label>
                    </div>

                    <div className="form-check">
                        <label className="form-check-label">
                        <input type="checkbox"
                            checked={this.state.COMPUTER_NETWORK}
                            onChange={this.toggleChange_COMPUTER_NETWORK}
                            className="form-check-input"
                        />
                        COMPUTER NETWORK
                        </label>
                    </div>
                    <div className="form-check">
                        <label className="form-check-label">
                        <input type="checkbox"
                            checked={this.state.MOBILE_PROGRAMMING}
                            onChange={this.toggleChange_MOBILE_PROGRAMMING}
                            className="form-check-input"
                        />
                        MOBILE PROGRAMMING
                        </label>
                    </div>
                    <div className="form-check">
                        <label className="form-check-label">
                        <input type="checkbox"
                            checked={this.state.PROGRAMMING_LANGUAGES}
                            onChange={this.toggleChange_PROGRAMMING_LANGUAGES}
                            className="form-check-input"
                        />
                        PROGRAMMING LANGUAGES
                        </label>
                    </div>
                    <div className="form-check">
                        <label className="form-check-label">
                        <input type="checkbox"
                            checked={this.state.DATABASE_THEORY}
                            onChange={this.toggleChange_DATABASE_THEORY}
                            className="form-check-input"
                        />
                        DATABASE THEORY
                        </label>
                    </div>
                    <div className="form-check">
                        <label className="form-check-label">
                        <input type="checkbox"
                            checked={this.state.SOFTWARE_ENGINEERING}
                            onChange={this.toggleChange_SOFTWARE_ENGINEERING}
                            className="form-check-input"
                        />
                        SOFTWARE ENGINEERING
                        </label>
                    </div>
                    <div className="form-check">
                        <label className="form-check-label">
                        <input type="checkbox"
                            checked={this.state.COMPUTER_SECURITY_PRIVACY}
                            onChange={this.toggleChange_COMPUTER_SECURITY_PRIVACY}
                            className="form-check-input"
                        />
                        COMPUTER SECURITY PRIVACY
                        </label>
                    </div>
                    <div className="form-group">
                        <button className="btn btn-primary">
                        Submit
                        </button>
                    </div>
                </form>


            </div>
        )
    }

}

export default AddSubscriptionForm