/* Function to autheticate the professor */
function loginRequest(e){
    e.preventDefault();
    if(validate){
        let request = {
            username: document.getElementById('username').value,
            password: document.getElementById('password').value,
        }

        // axios.post('http://localhost:8000/loginProfessor', request)
        axios.post('http://206.189.194.211:8000/loginProfessor', request)
        .then(response => {
            console.log(request)

            var goodRes = response.data.result
            var error = response.data.error
            //if the username is already in the database: then let the user know and dont redirect to confirmation page
            if (error === "Invalid Username" || error === "Invalid password"){
                alert(error)
            }

            //redirect to a page confirmation page if no error is sent back from the server
            else{
                var decode = jwt_decode(goodRes)
                //setting the cookie to store the professor package from the backend
                localStorage.setItem('professor_name', decode.username)
                localStorage.setItem('professor_id', decode.id)
                localStorage.setItem('user_type', decode.userType)
                window.location = "/professor";
            }
        })
        //Error handling: 
        .catch(err => {
            console.log(err);
            console.log(request);
        })
    }

    else{
        alert("Missing required fields!")
    }


}


/*User input validation */ 
function validate(){
    //gettting form input information
    var username = document.getElementById('username').value
    var password = document.getElementById('password').value
    let isValid = true;
    //username validation
    if(!username){
        isValid = false;

    }

    //password validation
    if(!password){
        isValid = false;
    }

    return isValid
}



