import React from 'react';
import "./AdminLogin.css"
import axios from 'axios';
 // @ts-ignore  
 import jwt_decode from "jwt-decode";
 class AdminLogin extends React.Component{
    render(){
        return(
            <div className="admin-login-control">
                <form onSubmit={(e) => adminLogin(e)}>
                    <div className="form-inner">
                        <h2>Admin Login</h2>
                        <div className="form-group">
                            <label htmlFor="name">User Name </label>
                            <input type="text" name="name" id="admin-user-name" />
                        </div>
                        <div className="form-group">
                            <label htmlFor="password">Password </label>
                            <input type="password" name="password" id="admin-password" />
                        </div>

                        <input type="submit" value="Login" />   

             
                    </div>
                </form>
            </div>

        )
    }
 }

 //Function to handle authenticating the admin user: 
 function adminLogin(e){
     e.preventDefault();
     let request = {
        username: document.getElementById('admin-user-name').value,
        password: document.getElementById('admin-password').value
    }
    console.log(document.getElementById('admin-user-name').value)
    console.log(document.getElementById('admin-password').value)
    
    //calling the API url
    axios.post('http://206.189.194.211:8000/loginAdmin', request)
    // axios.post('http://localhost:8000/loginAdmin', request)
    .then(response => {
        var goodRes = response.data.result
        var error = response.data.error

        //alert the error to the user
        if(error === "Invalid password" || error === "Invalid Username"){
            alert(error)
        }

        //otherwise process the response sending back from the BE
        else{
            //decode the jwt authentication package from the backend
            var decode = jwt_decode(goodRes);
            //setting the admin cookie session from the backend
            localStorage.setItem('adminName', decode.username)
            localStorage.setItem('adminID', decode.id)
            localStorage.setItem('isAdmin', decode.userType)

            window.location = "/AdminPage";

        }

    })
    .catch(err => {
        alert(err)
        console.log(err)
    })

 }

 export default AdminLogin