import React from 'react';
import axios from 'axios';
import AddSubscriptionForm from './AddSubscriptionForm';
import ProfessorDashboardBase from './ProfessorDashboardBase';
const professor_name = localStorage.getItem('professor_name')
const professor_id = localStorage.getItem('professor_id')
const user_type = localStorage.getItem('user_type')
class Professor extends React.Component{
    //creating a state to store the user data object from API response
    state = {
        professor: []
    }
    //Mounting an API to collect further information about the user data
    componentDidMount(){
        axios.get(`http://206.189.194.211:8000/getThisUser/${professor_id}/${user_type}`)
        // axios.get(`http://localhost:8000/getThisUser/${professor_id}/${user_type}`)
        .then(res => {
            const prof_info = res.data
            this.setState({professor: prof_info})
            // console.log(prof_info)
        })  
    }
    render(){
        //deconstructing the state
        const{
            professor
        } = this.state
        let subscription = professor.subscription
        // console.log()
        //if the professor already has a subscription list, take them to the main dashboard
        if(professor.subscription != null){
            return(
                <div>
                    <ProfessorDashboardBase {...subscription}/>
                </div>
            )
        }

        //otherwise, send them to a pop up to select courses/topic to follow
        else{
            return(
                <div>
                    <AddSubscriptionForm />
                </div>
            )
        }

    }
}

export default Professor