import React from "react"
import "./AdminPage.css"
import AdminNavBar from './AdminNavBar';
import { Grid } from "@material-ui/core";
// import QuestionStatsCardView from './QuestionStatsCardView';
import UserStatsCardView from './UserStatsCardView';
// import {FaUserAlt} from "react-icons/fa";
import DisplayQuestionAnswersForAdmin from './displayQuestionAnswersForAdmin';
import axios from 'axios';
import jwt_decode from "jwt-decode";
import AddAdminPopUp from "./AddAdminPopUp";

class AdminPage extends React.Component{
    
    //creating a state to store the user data object from the backend
    state = {
        students: [],
        tutors: [], 
        professors: []
    }
    //mounting the api call before the page is load
    componentDidMount(){
        //make an api call to the backend to get all user from
        axios.get('http://206.189.194.211:8000/getAllUserData')
        // axios.get('http://localhost:8000/getAllUserData')
        .then(res => {
            const users = res.data.result;
            var decode = jwt_decode(users)
            // console.log(decode.students)
            // console.log(decode.professors)
            this.setState({students: decode.students, tutors: decode.tutors, professors: decode.professors});
        })
    }
    render(){
        const myStyle = {
            textAlign: "center"
        }
        const {
            students, tutors, professors
        } = this.state
        //Error handling if the three objects are null when the front end recieves it.
        if(tutors != null && students === null && professors === null){
            var tutorsCount = tutors.length
            var studentsCount = 0
            var professorCount = 0
        }
        else if(students != null && tutors === null && professors === null){
            studentsCount = students.length
            tutorsCount = 0
            professorCount = 0
        }

        else if(professors != null && tutors === null && students === null){
            studentsCount = 0
            tutorsCount = 0
            professorCount = professors.length
        }

        else if(tutors && students && professors){
            tutorsCount = tutors.length
            studentsCount = students.length
            professorCount = professors.length

        }

        else if(professors === null){
            professorCount = 0
            tutorsCount = tutors.length
            studentsCount = students.length
        }

        else if(tutors === null){
            professorCount = professors.length
            tutorsCount = 0
            studentsCount = students.length
        }

        else if(students === null){
            professorCount = professors.length
            tutorsCount = tutors.length
            studentsCount = 0
        }


        else{
            tutorsCount = 0
            studentsCount = 0
            professorCount = 0
        }

        //Combining data together to display it onto the card view. 
        const userCountData = [
            {
                userType: 'STUDENTS',
                userCount: studentsCount
            },
            {
                userType: 'TUTORS',
                userCount: tutorsCount
            },
            {
                userType: 'PROFESSORS',
                userCount: professorCount
            }
        ]
        //Function to get all user obect from the database
        const getUserTypeObject = users => {
            return(
                <Grid item xs={12} sm={4}>
                    <UserStatsCardView {...users} />
                </Grid>
            )
        }
        return(
            <div className="admin-page-main-container">
                <div className="admin-page-nav-bar">
                    <AdminNavBar />
                    <br></br>
                </div>

                <div className="admin-page-functions">
                    <div className="cardView">
                        <div className="dashboard-header" style={myStyle}>
                            <h1>Dashboard</h1>
                            <div className="add-admin-pop-up" style={{float:"right", paddingRight:"50px"}}>
                                <AddAdminPopUp />

                            </div>
                        </div>
                            
                        <div className="cardView_01">
                            <Grid container spacing={4} style={{paddingLeft: "40px", paddingRight: "20px"}}>
                                {userCountData.map(users => getUserTypeObject(users))}
                            </Grid>
                            <br></br>
                            <br></br>
                        </div>
                    </div>
                    <DisplayQuestionAnswersForAdmin />
                </div>
            </div>
            
        )
    }
}


export default AdminPage;