import React from 'react';
import AdminNavBar from './AdminNavBar';
import QuestionAnswerInfoTableView from './QuestionAnswerInfoTableView'
import AnswerInfoTableView from './AnswerInfoTableView'
import axios from 'axios';
// import jwt_decode from "jwt-decode";
// import AdminPopUpAddUsers from "./AdminPopUpAddUsers";
import AdminPopUpUpdateUser from "./AdminPopUpUpdateUser";
import AdminPageDeleteUsersPopUp from "./AdminPageDeleteUsersPopUp";
const obj_type = localStorage.getItem('object_type')

class AdminQuestionAnswerActionPage extends React.Component{
    state = {
        questions: [],
        answers: []
    }
    //Mounting the API call: 
    async componentDidMount(){
        //making two api request for answer and question data
        const [questionRes, answerResp] = await Promise.all([
            axios.get('http://206.189.194.211:8080/getAllQuestions'),
            axios.get('http://206.189.194.211:8080/getAllAnswers')
        ]);
        //setting the state of the two api request into the state
        this.setState({
            questions: questionRes.data,
            answers: answerResp.data
        })
    }

    render(){
        const myStyle = {
            float: "right",
            paddingRight: "10px",
            paddingBottom: "10px",
            maxWidth: "100%",
            maxHeight: "100%",
            display: "flex",
            justifyContent: "flex-end",   
        }
        const myStyle2 = {
            paddingLeft:"10px"
        }
        const {
            questions, answers
        } = this.state
        console.log(answers)
        //Rendering the table based on the appropriate user type: 
        if(obj_type === "QUESTIONS"){
            return(
                <div>
                    <AdminNavBar />
                    <h1>Object Type: {obj_type}</h1>
                    <div className="admin-pop-up" style={myStyle}>
                        <AdminPopUpUpdateUser />
                        <div className="admin-pop-up-delete" style={myStyle2}>
                            <AdminPageDeleteUsersPopUp />                        
                        </div>
                    </div>
                    <QuestionAnswerInfoTableView {...questions}/>
                </div>
            )
        }
        else if(obj_type === "ANSWERS"){
            return(
                <div>
                    <AdminNavBar />
                    <h1>Object Type: {obj_type}</h1>
                    <div className="admin-pop-up" style={myStyle}>
                        <AdminPopUpUpdateUser />
                        <div className="admin-pop-up-delete" style={myStyle2}>
                            <AdminPageDeleteUsersPopUp />                        
                        </div>
                    </div>
                    <AnswerInfoTableView {...answers}/>
                </div>      
            )
        }

        else{
            return(
                <div>
                    <AdminNavBar />
                    <h1>Nothing to show here!</h1>
                </div>
      
            )
        }
    }
}

export default AdminQuestionAnswerActionPage