import React, {useEffect, useState } from 'react';
import Link from '@material-ui/core/Link';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Typography from '@material-ui/core/Typography';
import axios from 'axios';

// import Title from './Title';
function preventDefault(event) {
  event.preventDefault();
}

const useStyles = makeStyles((theme) => ({
  seeMore: {
    marginTop: theme.spacing(3),
  },
}));

export default function ProfessorSubscribedQuestion(props) {
  const [list, setList] = useState([]);
  const userSubscription = []
  for(const data in props){
    userSubscription.push(props[data])
  }

  // console.log(userSubscription)


  let request = {
    subscription: userSubscription
  }
  // console.log(request)

  useEffect(() =>{
    let mounted = true;
    axios.post('http://206.189.194.211:8081/getMatchingTopics', request)
    // axios.post('http://localhost:8081/getMatchingTopics', request)
    .then(response => {
      if(mounted){
        console.log(response.data)
        setList(response.data)
      }
    })
    return () => mounted = false;

  }, [])
  const classes = useStyles();
  return (
    <React.Fragment>
        <Typography component="h2" variant="h6" color="primary" gutterBottom>
            Based on your subscribed topic
        </Typography>
      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>ID</TableCell>
            <TableCell>CONCEPT</TableCell>
            <TableCell>TOPIC</TableCell>
            <TableCell>DESCRIPTION</TableCell>
            <TableCell>TIME AND DATE</TableCell>
            <TableCell>STATUS</TableCell>

          </TableRow>
        </TableHead>
        <TableBody>
          {list.map((item) => (
            <TableRow key={item._id}>
              <TableCell>{item._id}</TableCell>
              <TableCell>{item.course}</TableCell>
              <TableCell>{item.topic}</TableCell>
              <TableCell>{item.description}</TableCell>
              <TableCell>{item.timestamp}</TableCell>
              <TableCell>{item.status}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <div className={classes.seeMore}>
        <Link color="primary" href="#" onClick={preventDefault}>
          See more orders
        </Link>
      </div>
    </React.Fragment>
  );
}
