import React from 'react';
import AdminNavBar from './AdminNavBar';
import UserInfoActionTableView from './UserInfoActionTableView'
import axios from 'axios';
import jwt_decode from "jwt-decode";
import AdminPopUpAddUsers from "./AdminPopUpAddUsers";
import AdminPopUpUpdateUser from "./AdminPopUpUpdateUser";
import AdminPageDeleteUsersPopUp from "./AdminPageDeleteUsersPopUp";
const user_type = localStorage.getItem('user_type')

class AdminUserActionPage extends React.Component{
    //creating a state to store the user data object from the backend
    state = {
        students: [],
        tutors: []
    }
    //mounting the api call before the page is load
    componentDidMount(){
        //make an api call to the backend to get all user from
        axios.get('http://206.189.194.211:8000/getAllUserData')
        // axios.get('http://localhost:8000/getAllUserData')
        .then(res => {
            const users = res.data.result;
            var decode = jwt_decode(users)
            // console.log(decode.students.length)
            this.setState({students: decode.students, tutors: decode.tutors, professors: decode.professors});
        })
    }
    render(){
        const myStyle = {
            float: "right",
            paddingRight: "10px",
            paddingBottom: "10px",
            maxWidth: "100%",
            maxHeight: "100%",
            display: "flex",
            justifyContent: "flex-end",
            
        }
        const myStyle2 = {
            paddingLeft:"10px"
        }
        const {
            students, tutors, professors
        } = this.state
        if(user_type == "STUDENTS"){
            return(
                <div>
                    <AdminNavBar />
                    <h1>User Type: {user_type}</h1>
                    <div className="admin-pop-up" style={myStyle}>
                        <AdminPopUpUpdateUser />
                        <div className="admin-pop-up-delete" style={myStyle2}>
                            <AdminPageDeleteUsersPopUp />                        
                        </div>
                    </div>
                    <UserInfoActionTableView {...students}/>
                </div>
            )
        }
        else if(user_type == "TUTORS"){
            return(
                <div>
                    <AdminNavBar />
                    <h1>User Type: {user_type}</h1>
                    <div className="admin-pop-up" style={myStyle}>
                        <AdminPopUpAddUsers />
                        <div className="admin-pop-up-update" style={myStyle2}>
                            <AdminPopUpUpdateUser />
                        </div>
                        <div className="admin-pop-up-delete" style={myStyle2}>
                            <AdminPageDeleteUsersPopUp />                        
                        </div>
                    </div>
                    <UserInfoActionTableView {...tutors}/>
                </div>
            )
        }

        else if(user_type == "PROFESSORS"){
            return(
                <div>
                    <AdminNavBar />
                    <h1>User Type: {user_type}</h1>
                    <div className="admin-pop-up" style={myStyle}>
                        <AdminPopUpAddUsers />
                        <div className="admin-pop-up-update" style={myStyle2}>
                            <AdminPopUpUpdateUser />
                        </div>
                        <div className="admin-pop-up-delete" style={myStyle2}>
                            <AdminPageDeleteUsersPopUp />                        
                        </div>
                    </div>
                    <UserInfoActionTableView {...professors}/>
                </div>
            )
        }

        else{
            return(
                <div>
                    <AdminNavBar />
                    <h1>No Users to Show Here</h1>
                </div>
            )
        }

    }
}


export default AdminUserActionPage;
