/*Function to transfer to the admin action page to add and update users.
the function will take a user type as a paramaer and store it as a session key, depends of what type of cookie is, the admin action to user page will display the approprate user group*/
function getToAdminActionPage(userType){
  if(userType == "QUESTIONS" || userType == "ANSWERS"){
    window.location = "/AdminQuestionAnswerActionPage"
    localStorage.setItem("object_type", userType)

  }
  else{
    window.location = "/AdminUserActionPage"
    localStorage.setItem('user_type', userType)

  }
}