import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});


export default function AnswerInfoTableView(props) {
  const classes = useStyles();
  const rows = []
  for(const data in props){
    rows.push(props[data])
  }

  // for(var key in data){
  //   if(data.hasOwnProperty(key)){
  //     console.log(key+': '+data[key])
  //   }
  // }
  return (
    <TableContainer component={Paper}>
      <Table className={classes.table} aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>ID</TableCell>
            <TableCell>Question ID</TableCell>
            <TableCell align="right">STUDENT</TableCell>
            <TableCell align="right">TUTOR</TableCell>
            <TableCell align="right">CONTENT</TableCell>
            <TableCell align="right">TIME AND DATE</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.name}>
              <TableCell component="th" scope="row">
                {row._id}
              </TableCell>
              <TableCell align="right">{row.questionID}</TableCell>
              <TableCell align="right">{row.studentName}</TableCell>
              <TableCell align="right">{row.tutorName}</TableCell>
              <TableCell align="right">{row.answer}</TableCell>
              <TableCell align="right">{row.timestamp}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}

